import {  
    mapMutations  
} from 'vuex';
import api from '@/common/vmeitime-http/'
export default {
	data() {
		return {
			cartList: []
		};
	},
	onLoad(option){
		this.mycoloect()
	},
	onPullDownRefresh() {
		this.mycoloect(true)
	},
	methods:{
		mycoloect(flag) {
			uni.showLoading({title: '正在加载'})
			let mycoloecturl = '/collect/list?type=0&page=1&limit=100'
			api.httpget(mycoloecturl).then((res) => {
				let respons = res.data;
				if(respons.errno == 0) {
					this.cartList = respons.data.list
				}
				if(flag) uni.stopPullDownRefresh();
				uni.hideLoading()
			}).catch((err) => {
				uni.hideLoading()
				if(flag) uni.stopPullDownRefresh();
			})
		},
		deletes(id) {
			uni.showLoading({title: '正在取消'})
			let collecturl = '/collect/addordelete'
			api.httppost(collecturl,{type:0, valueId: id}).then((res) => {
				let respons = res.data;
				if(respons.errno == 0) {
					this.mycoloect()
				} else if (respons.errno == 501) {
					uni.navigateTo({
						url: '../public/login'
					})
				}
				uni.hideLoading()
			}).catch((err) => {
				uni.hideLoading()
			})
		},
		//详情
		navToDetailPage(item){
			//测试数据没有写id，用title代替
			let id = item.valueId;
			uni.navigateTo({
				url: `/pages/product/product?id=${id}`
			})
		},
	}
}
